body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Samim";
  src: local("Samim"), url(/iomier/eduapp/static/media/Samim.fa7346ef.woff) format(woff),
    url(/iomier/eduapp/static/media/Samim.1fbc03d1.woff2) format(woff2),
    url(/iomier/eduapp/static/media/Samim.72cafbdf.eot) format(eot),
    url(/iomier/eduapp/static/media/Samim.cc531f95.ttf) format(ttf);
}
a {
  color: #718093;
}
a:hover {
  text-decoration: none;
}
body {
  /* direction: rtl; */
  font-size: 13px;
  /* font-family: "samim"; */
  background-color: #ddd;
}
.container {
  margin: 1em auto;
}
.app {
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;

  background-color: #fff;
}
.side {
  background-color: #f5f6f8;
  border-top-right-radius: 10px;
}
.footer {
  background-color: #375975;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  height: 40px;
}
.home-grid {
  text-align: center;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin: 0;
  grid-gap: 10px;
  padding: 15px;
}
.chat-i {
  background-color: #bad;
  padding: 0.5em;
  text-align: justify;
  margin: 1em 0;
  border-radius: 10px;
}
.chat-o {
  background-color: #b00b;
  direction: rtl;
  text-align: justify;
  border-radius: 10px;
  padding: 0.5em;
  color: whitesmoke;
}
.sm-side {
  background-color: #eee;
}
.done {
  text-decoration: line-through;
}
i {
  font-size: 1.5em;
}
.iconBox {
  text-align: center;
  margin: 0.5em 0;
}
.slogan {
  font-size: 0.7em;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}
.selected {
  color: #1a1a1a;
}
.icon .fa,
.icon .fab,
.icon .fal,
.icon .far,
.icon .fas {
  display: block;
}
.vid {
  text-align: center;
}
.short-desc {
  padding: 0.5em;
  line-height: 23px;
}
iframe {
  width: 100%;
}
.thumb img {
  width: 100%;
}
.grid2 {
  display: grid;
  grid-row-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
}
.grid2 img {
  width: 80px;
  margin: auto;
}
.course-cat {
  border: black solid 1px;
  background-color: #eee;
  padding: 1em;
  border-radius: 10px;
}

.block {
  height: 400px;
  background-color: #bada55;
}
.logo {
  text-align: center;
  height: 50px;
}
.logo p {
  position: relative;
  top: 50%;
  -webkit-transform: perspective(1px) translateY(-50%);
          transform: perspective(1px) translateY(-50%);
}
.logo i {
  position: relative;
  top: 50%;
  -webkit-transform: perspective(1px) translateY(-50%);
          transform: perspective(1px) translateY(-50%);
}
.mid-man {
  position: relative;
  top: 50%;
  -webkit-transform: perspective(1px) translateY(-50%);
          transform: perspective(1px) translateY(-50%);
}

